.burger{
  cursor:pointer;
  
}
.menu-burger{
  position: absolute;
  -webkit-transform: translate3d(-13px, 41px, 10px);
          transform: translate3d(-13px, 41px, 10px);
  cursor: pointer;
  padding:30px;
  
     
}
.zoom{
  transform:scale(1.2) ;
  -webkit-transform:scale(1.2) ;
  -moz-transform:scale(1.2) ;
  -ms-transform:scale(1.2) ;
  -o-transform:scale(1.2) ;
}
.menu-burger span {
  content: "";
  position: absolute;
  height: 5px;
  width: 40px;
  background-color: rgb(178, 172, 199);

  transition: 1s ease;
  border-radius: 25%;
  

}

span:nth-child(0){
  -webkit-transform:translate3d(0,0,0);
          transform:translate3d(0,0,0) 
}
span:nth-child(1){
  -webkit-transform: translate3d(0,8px,0);
          transform: translate3d(0,8px,0);
}
span:nth-child(2){
   -webkit-transform:translate3d(0,17px,0);
           transform:translate3d(0,17px,0) 
}

