@media (min-width: 700px){
.q-hero{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size:16px;
   
}
.navigation.showMenu{
    display:block;
    width:20%;
    list-style-type: none;
    z-index:2;
}
.q-hero h1{
    font-size:40px;
}


.q-hero h1 img{
    margin-right: 25px;
}
.logo{
 margin:12px;
   height: 46px;
}
.logoStyle{
    height:46px;
}
.article1{
    height:50%;
}
.article2{
    height: 50%;
}
.buttons .article1 h2{
    font-size:25px;
    color:white;
}
.buttons .article2 h2{
    font-size:25px;
    color:rgb(31, 25, 25);
}
.buttons .article2 p{
    color:rgb(7, 5, 5);
    font-size: 15px;
}
.buttons .article1 p{
    color:white;
    font-size: 15px;
}
.buttons .article2 img{
    z-index: -1;
    height: 212px;
    margin-top: 35px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.buttons .btn{
    font-size: 20px;
}

.containers .container1{
    width:50%;
    text-align: center;
    font-size: 18px;
    
}
.slide h1{
    
    font-size: 36px;
}
.containers .container2{
    background:none;
}
.containers .container3{
    background:none;
}
.container2 img{
    
position: relative;
    width: 100%;
    height: 100%;
    margin: 0px;
    transform: translate(214%,-46%);

   

}


.slide .img-huitre{
    height: 138px;
}
.ancre{
    height:50px;
}
.container h1 span {
    font-size:2.3rem;
    
}

.containers .container3 h1 {
    text-align: start;
    font-size:58px;
    color:black;
    text-align:center
   
}

.containers .container3 p{
    color:black;
    text-align: center;
    font-size:20px;
    margin-left: 25px;
    max-width: 1000px;
}




}

@media (max-width: 700px){
    .sale-oyster img{
        margin-left: 1px;
    
    }
    .title-oyester{
        font-size: 50px;
    }
    .wrapper-oyster{
        height:130vh;
      }
      .containers .container1{
          margin:0;
      }
    .container3 p{
        padding:6px;
        color:rgb(31, 25, 25);
        box-shadow: 10px 5px 55px black;


    }
 
   .btn-choice{
   
    top: 15px;
    left: 152px;
    margin-top:17px;
    
   }
}
@media (max-width: 320px){
    .Menu-choice{
       left:0;
    }
    
    
}
    @media (max-width: 420px){
    .q-hero button{
transform: translate3d(0,-16px,0);   
 }

